/* m-plus-1p-regular - latin_cyrillic */
@font-face {
  font-family: 'M PLUS 1p';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/m-plus-1p-v19-latin_cyrillic-regular.eot');
  /* IE9 Compat Modes */
  src: local('M PLUS 1p'), local('MPLUS1p-Regular'),
    url('../fonts/m-plus-1p-v19-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */
    url('../fonts/m-plus-1p-v19-latin_cyrillic-regular.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('../fonts/m-plus-1p-v19-latin_cyrillic-regular.woff') format('woff'),
    /* Modern Browsers */
    url('../fonts/m-plus-1p-v19-latin_cyrillic-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */
    url('../fonts/m-plus-1p-v19-latin_cyrillic-regular.svg#MPLUS1p') format('svg');
  /* Legacy iOS */
}

